    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    justify-items: center;
    -webkit-align-items: center;
            align-items: center;
    margin: 0 auto;
    width: 60%;
    margin-left: 20%;
    margin-right: 20%;
    height: 100%;
}

.InfoNav, .ProjectsNav, .HomeNav{
    padding-top: 1vh;
    padding: 2%;
