            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    position: absolute;
    top: 45vh;
    bottom: 50vh;
    left: 30vh;
    right: 30vh;
    color: white;
    box-sizing: border-box;
}

#startButton, #homeSectionButton, #greetingSectionButton, #projectsSectionButton, #aboutMeSectionButton, #contactSectionButton{
    background:none!important;
    border:none; 
    font-family: "Montserrat";
    cursor: pointer;
    padding-top: 1vh;
    padding: 2%;
    font-size: 130%;
    outline: none;    
    -webkit-transition: 0.4s ease-in-out;    
    transition: 0.4s ease-in-out;
    color: grey;
}

#homeSectionButton{
    margin-left: 20%;
}

#homeSectionButton:hover{
    color:brown;
}

#greetingSectionButton:hover{
    color:brown;
}

#projectsSectionButton:hover{
    color:brown;
}

#aboutMeSectionButton:hover{
    color:brown;
}

#contactSectionButton:hover{
    color:brown;
}

